
import {createRouter,createWebHistory} from 'vue-router';

import HomePage from './pages/HomePage.vue';
import DanceStyle from './pages/Style/DanceStyle.vue';
import SignUp from './components/UI/SignUp.vue';
import ExploreClasses from './components/cards/ExploreClasses.vue';
import AllClasses from './pages/Style/AllClasses.vue';

import store from './store/index.js';

const router= createRouter({
    history: createWebHistory(),
    routes:[
        {path:'/', redirect:'home'},
        {path:'/home', component:HomePage},
        {path:'/indianstyle',children:[
            {path:'bollywood', component: DanceStyle, children:[
                {path:'exploreclasses', component: ExploreClasses,meta:{requiresAuth:true}}
        ]},
            {path:'bharatnatyam', component: DanceStyle},
            {path:'kathak', component: DanceStyle},
        ]},
        {path:'/internationalstyle',children:[
            {path:'ballet', component: DanceStyle},
            {path:'contemporary', component: DanceStyle},
            {path:'flamenco', component: DanceStyle},
        ]},
        {path:'/signup', component:SignUp,meta:{requiresUnauth:true}},
        {path:'/exploreclasses', component:AllClasses, meta:{requiresAuth:true} }
    ]
})

router.beforeEach((to,_,next)=>{ 
    if(to.meta.requiresAuth && !store.getters['auth/isAuthenticated']){
        next('/signup');
    }else if(to.meta.requiresUnauth && store.getters['auth/isAuthenticated']){
        next('/home')
    }else{
        next();
    }

})

export default router;