<template>
    <div class="jumbotron vertical-center">
        <div class="container">
            <div class="row align-items-center">
            <div class="col">
                <h1>{{ danceform }}</h1>
                <p>{{ danceformdetail }}</p>
            </div>
            <div class="col">
                <div class="resize">
                    <img
                    :src="getImgUrl()"
                    alt=""
                    class="rounded-circle float-right img-fluid size"
                    />
                </div>
            </div>
            </div>
        </div>
    </div>
    <!-- require(`@/assets/${filename}`) -->
</template>

<script>
export default {
    data(){
        return{
            filename:''
        }
    },
    props:['danceform','danceformdetail'],
    methods:{
        getImgUrl(){
            if(this.danceform.toLowerCase()=="bollywood"){
                return require('../../assets/contemporary.jpg')
            }
            if(this.danceform.toLowerCase()=="bharatnatyam"){
                return require('../../assets/bharatnatyam.jpeg')
            }
            if(this.danceform.toLowerCase()=="kathak"){
                return require('../../assets/kathak.jpg')
            }
            if(this.danceform.toLowerCase()=="ballet"){
                return require('../../assets/ballet.jpeg')
            }
            if(this.danceform.toLowerCase()=="contemporary"){
                return require('../../assets/contemporary.jpg')
            }
            if(this.danceform.toLowerCase()=="flamenco"){
                return require('../../assets/flamenco.jpg')
            }
        }
    }
    
}
</script>

