<template>
  <div class=" align-items-center add">
    <div class="card">
      <!-- <img src="../../assets/logo.png" class="card-img-top" alt="..." /> -->
      <iframe
        :src="getvideolink()"
      >
      </iframe>
      <div class="card-body">
        <h5 class="card-text">{{danceform}}</h5>
        <p class="card-text">
          {{song}} with {{tutor}}
        </p>
      </div>
    </div>
  </div>
  <!-- container d-flex flex-wrap -->
</template>

<script>
export default {
  data(){
    return{
      song:'',
      tutor:'',
      videolink:''
    }
  },
  props:{
    'item':Object,
    'danceform':String
  }
  /* props:
  [{
    item: {type: Object}
  },
  'danceform'
  ] */,
  mounted(){
    this.song=JSON.parse(JSON.stringify(this.item.item.song))
    this.tutor=JSON.parse(JSON.stringify(this.item.item.tutor))
    this.videolink=JSON.parse(JSON.stringify(this.item.item.videolink))
  },
  methods:{
    getvideolink(){
      return `https://www.youtube.com/embed/${this.videolink}`
    }
  }
}
</script>

