<template>
    <div class="jumbotron vertical-center">
        <div class="container">
            <form @submit.prevent="submitForm">
                <div class="card align-middle">
                  <div class="text-center">
                    <h3>Create an account</h3>
                    <span class="abt">Already have an account? <a @click="changemode" class="auth">Sign In</a></span>
                  </div>
                  <div class="form mt-3">
                    <div class="inputbox">
                      <input type="text" v-if="auth=='signup'" class="form-control" placeholder="Full Name" v-model="name" />
                    </div>
                    <div class="inputbox">
                      <input type="text" class="form-control" placeholder="Email" v-model="email"/>
                    </div>
                    <div class="inputbox">
                      <input type="password" class="form-control" placeholder="Password" v-model="password"/>
                    </div>
                  </div>
                  <p class="error">{{error}}</p>
                  <div class="mt-4 proceed" >
                    <button
                      class="
                        btn btn-primary btn-block
                        d-flex
                        flex-row
                        justify-content-between
                        align-items-center
                      "
                    >
                      <div class="text-right"><span>{{toggleAuthButton()}}</span></div>
                      <div class="text-right">
                        <span><i class="fa fa-long-arrow-right align-items-center"></i></span>
                      </div>
                    </button>
                  </div>
    
                </div>
            </form>
        </div>
    </div>
</template>
<script>
/* import axios from 'axios'; */

export default {
    data(){
        return{
            name:'',
            email:'',
            password:'',
            auth:'signup',
            error:''
        }
    },
    methods:{
        async submitForm(){
            const actionpayload={
                    name:this.name,
                    email:this.email,
                    password:this.password
                }
            try{
                if(this.auth=='signup'){
                    await this.$store.dispatch('auth/signup',actionpayload)
                }
                await this.$store.dispatch('auth/signin',{
                    email:this.email,
                    password:this.password
                })
                this.name=this.$store.getters['auth/loggedinUser']
                const redirectURL= '/'+ (this.$route.query.redirect || 'home');
                this.$router.replace(redirectURL)

            }
            catch(error){
              this.error=error.message;
              console.log(error)
            }
            this.email=''
            this.password=''
        },
        changemode(){
            this.auth='signin'
        },
        toggleAuthButton(){
            if(this.auth=='signup')
                return 'Sign Up'
            if(this.auth=='signin')
                return 'Sign In'

        }
    }
}
</script>


