import {createStore} from 'vuex';
import createPersistedState from 'vuex-persistedstate'
import AuthModule from './auth/index';
import IndianModule from './exploreclasses/indianstyle/index';
import InternationalModule from './exploreclasses/internationalstyle/index';

const store= createStore({
    plugins: [createPersistedState()],
    modules:{
        auth:AuthModule,
        indianstyle:IndianModule,
        internationalstyle:InternationalModule
    }
})

export default store;