<template>
  <div
    class="modal fade"
    id="exampleModal"
    tabindex="-1"
    aria-labelledby="exampleModalLabel"
    aria-hidden="true"
  >
    <div class="modal-dialog">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="exampleModalLabel">
            Questions? Feel free to contact us
          </h5>
          <button
            type="button"
            class="btn-close"
            data-bs-dismiss="modal"
            aria-label="Close"
          ></button>
        </div>
        <form @submit.prevent="submitForm">
        <div class="modal-body">
            <div class="form-group row space">
              <label for="name" class="col-sm-2 col-form-label">Name:</label>
              <div class="col-sm-10">
                <input type="text" class="form-control" id="name" v-model="name"/>
              </div>
            </div>
            <div class="form-group row space">
              <label for="email" class="col-sm-2 col-form-label">Email:</label>
              <div class="col-sm-10">
                <input type="email" class="form-control" id="email" v-model="email"/>
              </div>
            </div>
            <div class="form-group row space">
              <label for="phone" class="col-sm-2 col-form-label" >Phone:</label>
              <div class="col-sm-10">
                <input type="tel" class="form-control" id="phone" v-model="phone"/>
              </div>
            </div>
            <div class="form-group row space">
              <label for="phone" class="col-sm-2 col-form-label"
                >Form:</label
              >
              <div class="col-sm-10">
                <select class="form-control form-control" v-model="danceform">
                  <option>Bollywood</option>
                  <option>Bharatnatyam</option>
                  <option>Kathak</option>
                  <option>Ballet</option>
                  <option>Contemporary</option>
                  <option>Flamenco</option>
                </select>
              </div>
            </div>
            <p v-if="response!=''">{{response}}</p>
        </div>
        <div class="modal-footer">
          <button
            type="button"
            class="btn btn-secondary"
            data-bs-dismiss="modal"
          >
            Close
          </button>
          <button class="btn btn-primary">Submit</button>
        </div>
        </form>
      </div>
    </div>
  </div>
</template>

<style scoped>
.card {
  border-radius: 10px;
  padding: 35px 26px;
  width: 400px;
  margin-left: auto;
  margin-right: auto;
}
.space {
  margin-bottom: 5px;
}
</style>

<script>
import axios from 'axios';
export default {
    data(){
        return{
            name:'',
            email:'',
            phone:null,
            danceform:'',
            response:''
        }
    },
    methods:{
        async submitForm(){
            try{
                const contact= await axios.post('/api/dancedetails/contact',{
                    name:this.name,
                    email:this.email,
                    phone:this.phone,
                    danceform:this.danceform
                })
                if(contact){
                    this.response="We received your query.."
                }
                this.name='',
                this.email='',
                this.phone='',
                this.danceform=''
            }
            catch(error){
                this.response="Oops! Sorry for interruption, we will get back to you"
                console.log(error)
            }
        }
    }
}
</script>