<template>
  <div>
    <base-detail :danceform="title" :danceformdetail="detail"></base-detail>
    <hr v-if="issignedIn()"> 
    <h1 v-if="issignedIn()">Explore our classes</h1>
    <div class="d-flex flex-wrap justify-content-around" v-if="issignedIn()">
      <explore-classes v-for="item in classes" :key="item._id" :item="{item}" :danceform="title"></explore-classes>
    </div>
    
  </div>
</template>

<script>
import BaseDetail from "../../components/UI/BaseDetail.vue";
import ExploreClasses from "../../components/cards/ExploreClasses.vue";
import axios from "axios";

export default {
  data() {
    return {
      title: "",
      detail: "",
      route: "",
      fileName: "",
      classes: []
    };
  },
  components: {
    BaseDetail,
    ExploreClasses,
  },
  created() {
    this.route = this.$route.path.split("/");
    /*  this.route = this.route.split("/"); */
    if(this.route.length==4) 
        this.route = this.route[this.route.length - 2];
    if(this.route.length==3) 
        this.route = this.route[this.route.length - 1];
    this.loadDance();
  },
  methods: {
    async loadDance() {
      try {
        const dance = await axios.get("/api/dancedetails");
        for (let i = 0; i < dance.data.length; i++) {
          if (this.route.toLowerCase() == dance.data[i].form.toLowerCase()) {
            this.title = dance.data[i].form;
            this.detail = dance.data[i].details;
            this.fileName = dance.data[i].filename;
            
            this.classes=dance.data[i].classes
            break;
          }
        }
        
      } catch (error) {
        console.log(error);
      }
    },
    issignedIn() {
      return this.$store.getters["auth/isAuthenticated"]
     /*  const path=this.$route.path+'/exploreclasses'
      return path; */
      /* return this.$store.getters["auth/isAuthenticated"]; */
    },
  },
};
</script>

